window.dom = {
    // å¢ž
    create(string) {
        // åˆ›å»ºèŠ‚ç‚¹ 
        let template = document.createElement('template')
            // åŽ»é™¤æ–‡æœ¬
        template.innerHTML = string.trim()
            // template ä¸­çš„å…ƒç´ éœ€è¦é€šè¿‡contentå±žæ€§èŽ·å–
        return template.content.firstChild
    },
    after(node, node2) {
        // æ–°å¢žå¼Ÿå¼Ÿ
        node.parentNode.insertBefore(node2,node.nextSibling)
    },
    before(node, node2) {
        // æ–°å¢žå“¥å“¥
        node.parentNode.insertBefore(node2,node)
    },
    append(parent, child) {
        // æ–°å¢žå„¿å­
        parent.appendChild(child)
    },
    wrap(node, string) {
        // æ–°å¢žçˆ¸çˆ¸
        let parent = this.create(string)
        this.before(parent,node)
        this.append(parent,node)
    },
    // åˆ 
    remove(node) {
        node.parentNode.removeChild(node)
        return node
    },
    empty(node) {
        const list = []
        let  x  = node.firstChild
        while(x) {
            list.push(this.remove(x))
            x = node.firstChild
        }
        return list
    },
    // æ”¹ 
    attr(node, name, value) {
        // ä¿®æ”¹èŠ‚ç‚¹å±žæ€§
        if (arguments.length === 3) {
            // æ”¹å±žæ€§
            node.setAttribute(name ,value)
        } else if(arguments.length === 2) {
            // æŸ¥å±žæ€§
            return node.getAttribute(name)
        }
    },
    text(node, string) {
        if (arguments.length === 2) {
            // è®¾ç½®å†…å®¹
            if('innerText' in node){
                // é€‚é…ie
                node.innerText = string 
            }else{
                // å…¶ä»–æµè§ˆå™¨
                node.textContent = string 
            }

        } else if(arguments.length === 1) {
            if('innerText' in node){
                // é€‚é…ie
                return node.innerText 
            }else{
                // å…¶ä»–æµè§ˆå™¨
                return node.textContent 
            }
        } 
    },
    html(node, string) {
        if (arguments.length === 2) {
            // è®¾ç½®å†…å®¹     
            node.innerHTML = string 
        } else if(arguments.length === 1) {
            // æŸ¥æ‰¾å†…å®¹
            return node.innerHTML  
        } 
    },
    style(node, name, value) {
        if (arguments.length === 3) {
            node.style[name] = value
        } else if (arguments.length === 2) {
            if (typeof name === 'string') {
                // è¯»
                return node.style[name]
            } else if(name instanceof Object) {
                const object = name
                for (const key in object) {
                    if (Object.hasOwnProperty.call(object, key)) {
                        node.style[key] = object[key]
                    }
                }
            }
        }
    },
    class: {
        add(node, className) {
            node.classList.add(className)
        },
        remove(node, className) {
            node.classList.remove(className)
        },
        has(node, className) {
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn) {
        node.removeEventListener(eventName, fn)
    },
    // æŸ¥
    find(selector, scope) {
        return (scope ||ã€€document).querySelectorAll(selector)
    },
    parent(node) {
        return node.parentNode
    },
    children(node) {
        return node.children
    },
    siblings(node) {
        return Array.from(node.parentNode.children).filter( n => 
            n !== node )
    },
    next(node) {
        let x = node.nextSibling
        while(x && x.nodeType === 3){
            x = x.nextSibling
        }
        return x
    },
    previous(node) {
        let x = node.previousSibling
        while(x && x.nodeType === 3){
            x = x.previousSibling
        }
        return x
    },
    each(nodes, fn) {
        Array.from(nodes).forEach(element => {
            fn.call(null, element)
        })
    },
    index(node) {
        const list = node.parentNode.children
        for (let i = 0; i < list.length; i++) {
            if (node === list[i]) {
                return i           
            }   
        }
        return -1
    }
}